<template>
    <div class="container">
    <ul class="nav">
    <li v-for="item in categorys" :key="item.id">
    <button class="button" @click="setCategorys(item)">{{ item }}</button>
    </li>
    </ul>
    </div>
</template>
<script>
import {mapState, mapActions } from 'vuex';
export default {
    computed: {
        ...mapState(["categorys"])
    },
    methods: {
        ...mapActions(["getCategory", "setCategorys"])
    },
    mounted(){
        this.getCategory();
    }
}
</script>
