import { createStore } from "vuex";
const store = createStore({
  state: {
    categorys: null,
    products: null,
    currentProduct: {},
  },

  mutations: {
    setCategory(state, payload) {
      state.categorys = payload;
    },
    getProducts(state, products) {
      state.products = products;
    },
    currentProd(state, payload) {
      state.currentProduct = payload;
    },
    setProducts(state, payload) {
      state.products = payload;
    },
  },

  actions: {
    async getCategory({ commit }) {
      let res = await fetch(`https://dummyjson.com/products/categories`);
      let categorys = await res.json();
      commit("setCategory", categorys);
    },

    async setCategorys({ commit }, categorys) {
      let res = await fetch(
        `https://dummyjson.com/products/category/${categorys}`
      );
      let { products } = await res.json();
      commit("setProducts", products);
    },

    async getProducts({ commit }) {
      let res = await fetch(`https://dummyjson.com/products?limit=100`);
      let { products } = await res.json();
      commit("getProducts", products);
    },

    currentProd({ commit }, obj) {
      commit("currentProd", obj);
    },
  },

  getters: {},
});

export default store;
