<template>
  <div class="container box">
    <div class="card" v-for="item in products" :key="item.id">
      <img :src="item.thumbnail" alt="" class="img" />
      <h2 class="brand">Brand:{{ item.brand }}</h2>
      <h2 class="title">{{ item.title }}</h2>
      <p class="text">{{ item.description }}</p>
      <h2 class="price">Price:{{ item.price }}$</h2>
      <h2 class="stock">Stock:{{ item.stock }}</h2>
      <router-link :to="'/product/' + item.id" >
        <button class="button2"  @click="currentProd(item)">product</button>
      </router-link>
    </div>
  </div>
</template>
<script>
import { mapState, mapActions } from "vuex";
export default {
  computed: {
    ...mapState(["products"]),
  },
  methods: {
    ...mapActions(["getProducts"]),
    currentProd(obj) {
        this.$store.dispatch('currentProd', obj)
    }
  },
  created() {
    this.getProducts();
  },
};
</script>
